import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import styled from 'styled-components';
import toastr from 'toastr';

import { requestor } from '../common';

import ApplicationFormLayout from '../Component/ApplicationFormLayout';

import InputBox from '../Component/InputBox';
import RadioBox from '../Component/RadioBox';
import AlertMessageBox from '../Component/AlertMessageBox';

const SubmitButton = styled.button`
    border: none;
    width: 100%;
    text-align: center;
    padding: 16px 0;
    font-size: 14px;
    color: #1f1f1f;
    margin-top: 34px;
    background: #ffffff;
`;


const AlertConfirmButton = styled.button`
    border: none;
    background: #999999;
    width: 100%;
    margin-top: 20px;
    font-size: 14px;
    color: #ffffff;
    padding: 12px 0;
`;

export default class extends Component{
    constructor() {
        super();

        this.onChangeInput = this.onChangeInput.bind(this);
        this.state = {
            isComplete: false,
            isAlertOpen: false,
            formData: {
                name: '',
                password: '',
                phone: '',
                belong: '',
                email: '',
                area: '',
                sex: '',
                position: ''
            }
        }
    }

    render() {
        const idIconStyle = {
            width: '17px',
            height: '17px',
            margin: '13px 0'
        };
        const passwordIconStyle = {
            width: '14px',
            height: '20px',
            margin: '14px 0'
        };
        const inputStyle = {
            width: '100%'
        };

        return this.state.isComplete ?
        <Redirect to="/"/> : 
        (<section>
            {this.state.isAlertOpen && <AlertMessageBox title={this.state.alertTitle} message={this.state.alertMessage}>
                <AlertConfirmButton onClick={this.onClickAlertConfirmButton.bind(this)}></AlertConfirmButton>
            </AlertMessageBox>}
            <ApplicationFormLayout onSubmit = {this.onSubmitApplyForm.bind(this)} formDescription="    ." formTitle="">
                <div>
                    <InputBox inputType="text" iconImage="ic-person.png" iconStyle={passwordIconStyle} placeholder=" ." style={inputStyle} name="name" onChange={this.onChangeInput} value={this.state.name}/>
                    <InputBox inputType="text" iconImage="ic-work.png" iconStyle={passwordIconStyle} placeholder=" ." style={inputStyle} name="belong" onChange={this.onChangeInput} value={this.state.belong}/>
                    <RadioBox category="" items={[{label: '', name: 'sex', value:'MAN', onChange: this.onChangeInput}, {label: '', name: 'sex', value:'WOMAN', onChange: this.onChangeInput}]}/>
                    <InputBox inputType="text" iconImage="ic-call.png" iconStyle={passwordIconStyle} placeholder=" ." style={inputStyle} name="phone" onChange={this.onChangeInput} value={this.state.phone}/>
                    <InputBox inputType="email" iconImage="ic-email.png" iconStyle={idIconStyle} placeholder=" ." style={inputStyle} name="email" onChange={this.onChangeInput} value={this.state.email}/>
                    <InputBox inputType="password" iconImage="ic-lock.png" iconStyle={passwordIconStyle} placeholder=" ." style={inputStyle} name="password" onChange={this.onChangeInput} value={this.state.password}/>
                    <RadioBox category="" items={[{label: '', name: 'area', value: "LIFE", onChange: this.onChangeInput}, {label: '', name: 'area', value:"GAME", onChange: this.onChangeInput}]}/>
                    <RadioBox category="" items={[{label: '', name: 'position', value: "DEVELOP", onChange: this.onChangeInput}, {label: '', name: 'position', value: "DESIGN", onChange: this.onChangeInput}]}/>

                    <SubmitButton type="submit"></SubmitButton>
                </div>
            </ApplicationFormLayout>
        </section>
        );
    }

    async onChangeInput(event) {
        const { name: inputName, value } = event.target;

        await this.setState({
            formData: {
                ...this.state.formData, 
                ...{ [inputName]: value }
                }
            });
    }

    onSubmitApplyForm(event) {
        event.preventDefault();
        
        const isValidate = this.validateForm(this.state.formData);

        if (!isValidate) {
            toastr.error('     ');

            return;
        }

        requestor({ 
            url: '/api/apply',
            method: 'POST',
            headers: {
                'Access-Control-Allow-Origin': '*',
                'Content-Type': 'application/json'
            },
            crossDomain: true,
            data: this.state.formData
        })
        .then(response => {
            this.setState({ 
                isAlertOpen: true, 
                alertTitle: '', 
                alertMessage: `.
                <3333-06-9303547  > 5,000 
                 .` });
    
        })
        .catch(error => {
            const errorData = error.response.data;
            const statusCode = error.response.status;

            switch(statusCode) {
                case 400:
                    const { errors } = errorData;

                    Array.isArray(errors) && errors.forEach(customError => {
                        toastr.error(customError.defaultMessage);
                    });
                    break;

                case 409: 
                    const { message } = errorData;

                    toastr.error(message);
                    break;

                default: 
                    toastr.error('    .   .');
                    break;
            }
        });
    }

    validateForm(formData) {
        for (let key in formData) {
            if(!formData[key].trim()) return false;
        }

        return true;
    }

    onClickAlertConfirmButton() {
        this.setState({ isAlertOpen: false, isComplete: true });
    }
}


